#skipNavi{position:fixed; width:100%; margin:0 auto; z-index:9999; top:0; left:0;}
#skipNavi a{display:block; position:absolute; left:0; top:0; z-index:9999; width:0; height:0; font-size:0; overflow:hidden;}
#skipNavi a:active, #skipNavi a:focus{width:100%; height:auto; background:#000; color:#fff; font-size:1em; text-align:center; padding:5px 0;}

.slide_wrap {overflow:hidden;}
.layout_wrap {
	max-width:1200px; margin:0 auto; box-sizing:border-box;
	@include pc-guide(){
		padding:0 50px; border:1px solid red;
	}
}

@include pc-small(){
	.pc_show{display:block;}
	.m_show{display:none;}
}
@include mob(){
	.pc_show{display:none;}
	.m_show{display:block;}
}
.common_popup {
	height:100vh;
	.popup {
		display:flex;justify-content:center;align-items:center;
		.layout_wrap {position:relative;}
		.popup_content {
			width:100%; height:300px; background-color:$white;
			@include borderRadius(27px);
		}
		.btn_popup_close {
			width:20px; height:20px;background:url(../../images/common/close_x_btn.png) 0 0 no-repeat;
			background-size:cover;position:absolute;right:26px; top:28px;
		}
	}
}
/* popup Style */
.popup {
	position:fixed; top:0; left:0; width:100%; height:100%; z-index:999;
	justify-content:center; align-items:center; display:none;
	.popup_wrap {
		width:100%; position:relative; z-index:2;
	}
	.frame_wrap {
		padding:56.25% 0 0; position:relative;
		iframe {width:100%; height:100%; position:absolute; top:0; left:0;}
	}
	.btn_popup_close {
		width:20px; height:20px;background:url(../../images/common/close_x_btn.png) 0 0 no-repeat;
		background-size:cover;position:absolute;right:26px; top:28px;
	}
	.btn_popup_close {
		position:absolute; top:0; right:0; z-index:3; background-color:red;
		@include textBackground(50px, 50px);
		@include backgroundSoltC()
	}
	.dimmed {width:100%; height:100%; background:rgba(0, 0, 0, 0.5); position:absolute; top:0; left:0; z-index:1;}
}
/* scroll bar custom */
.scroll_bar_custom {
	padding:0 13px 0 13px; overflow-y:scroll;
	/*height:100px;*/ // 높이값 설정
	@include boxSizing;
	/* 스크롤바 설정*/
	&::-webkit-scrollbar {
		width:6px;
	}
	/* 스크롤바 막대 설정*/
	&::-webkit-scrollbar-thumb {
		height:17%; background-color:$gray2;
		/* 스크롤바 둥글게 설정  */
		border-radius:10px;
	}
	/* 스크롤바 뒷 배경 설정*/
	&::-webkit-scrollbar-track {
		background-color:transparent; // 투명
	}
}


/* Form Style */
.form_text_type01 {
	div {position:relative;}
	label {
		color:$gray5; font-size:16px; position:absolute; top:50%; left:24px; pointer-events:none;
		@include transition(all, 0.3s);
		@include translate(0, -50%);
		&.required {
			&:before {content:"* "; color:$red;}
		}
	}
	input {
		font-size:16px; padding:17px 25px; width:100%; display:block; border:1px solid $gray2; border-radius:10px; box-sizing:border-box;
		&.validField {padding:27px 25px 7px; outline:none; border-color:$gray2;}
		&.validField:focus {border-color:$blue5;}
		&.validField ~ label {
			top:10px; font-size:12px;
			@include translate(0, 0);
		}
	}
	span {font-size:12px; padding:7px 24px 0; display:none;}
	.errorField {
		input {border-color:$red;}
		input:focus {border-color:$red;}
		& ~ span {color:$red; display:block;}
	}
	.missingField {
		input {border-color:$red;}
		input:focus {border-color:$red;}
		& ~ span {color:$red; display:block;}
	}
}

